<template>
  <!--从后端去数据，然后填到相应的位置，还需要改一下页面的格式-->
  <div class="deviceDetail">

    <div class="appDetail_topbar">
      <h1 style="margin:10px;">设备详情：</h1>

    </div>
    <div class="setApp_content" style="position:absolute;top:85px;">
      <div class="comp_name" style="width:800px;">
        <h2 class="deviceDetailHead">一、设备详细信息：</h2>
        <!--<span class="content"> {{this.dev_id}}</span>-->
        <Row style="width:100%;">
          <Col span="12">
          <p class="deviceContentHead">设备种类 :<span class="content">{{this.deviceCategory}}</span></p>
          <p class="deviceContentHead">设备品种 :<span class="content">{{this.deviceKind}}</span></p>
          <p class="deviceContentHead">单位内编号 :<span class="content">{{this.eqComCode}}</span></p>
          <p class="deviceContentHead">登记机关 :<span class="content">{{this.acceptorAgencyName}}</span></p>

          </Col>
          <Col span="11">
          <p class="deviceContentHead">设备类别 :<span class="content">{{this.deviceClass}}</span></p>
          <p class="deviceContentHead">设备代码 :<span class="content">{{this.eqCode}}</span></p>
          <p class="deviceContentHead">使用单位填表人员 :<span class="content">{{this.comTablePerson}}</span></p>
          <p class="deviceContentHead">使用登记证编号 :<span class="content">{{this.registCode}}</span></p>

          </Col>

        </Row>

      </div>
      <div class="apply_type">
        <h2 class="applyDetailHead">二、已有申请详细信息：</h2>
        <!--<span class="content"> {{this.dev_id}}</span>-->
        <ul>
        <li v-for="(item,key,index) in this.logs" style="margin-bottom:10px;">
          <p class="applyTypeHead">{{key+1}}、 {{item.applyType}}</p>
          <p class="applyTypeContent"> 申请时间:{{item.updateTime}}</p>
          <Button type="primary" style="margin:10px;" @click="appDetail(item.applyId)">查看{{item.applyType}}详情</Button>


        </li>
        </ul>

      </div>
      <!--<div id="echartContainer" style="width:500px; height:500px"></div>-->


    </div>
  </div>
</template>
<script>

  import {mapActions, mapState, mapGetters} from 'vuex'
  import detailPdf from '../../../components/detailpdf/detailPdf.vue'
  import * as avaivbleService from '../../../services/avaiableDev.js'
  var echarts = require('echarts/lib/echarts');

  export default {
    data() {
      return {
        ruleForm: [],
        dev_id: '',
        dev_name: '',
        deviceSortNum: '',
        value1: '',
//        pdfUrl: {
//          锅炉能效证明: 'https://cdn.mozilla.net/pdfjs/tracemonkey.pdf',
//          水壶: 'https://cdn.mozilla.net/pdfjs/tracemonkey.pdf',
//          水壶2: 'https://cdn.rawgit.com/mozilla/pdf.js/c6e8ca86/test/pdfs/calrgb.pdf',
//          水壶3: 'https://cdn.rawgit.com/mozilla/pdf.js/c6e8ca86/test/pdfs/annotation-link-text-popup.pdf',
//          水壶4: 'https://cdn.rawgit.com/sayanee/angularjs-pdf/68066e85/example/pdf/relativity.protected.pdf',
//        },
        pdfUrl: {},
        pdfNum: 0,
        accStatus: '',
        accReason: '',

        approvalStatus: '',


        approvalRejValue: '',
        approvalReason: '',
        orderState: '',
        ruleForms: [],
        appComName: '',
        registPdfUrl: '',
        ruleForm: {},
        current: 0,
        applyId: '',

        //申请类别
        applyType: '',
        active: 0,
        deviceCategory: '',
        deviceClass: '',
        deviceKind: '',
        eqCode: '',
        eqComCode: '',
        acceptorAgencyName: '',
        comTablePerson: '',
        registCode: '',
        logs:[],
        useComName:'',

      }
    },

    activated() {
      const _this = this;
      _this.initData();
      var myChart = echarts.init(document.getElementById('echartContainer'));
// 绘制图表
      myChart.setOption({
        title: { text: 'ECharts 入门示例' },
        tooltip: {},
        xAxis: {
          data: ["衬衫","羊毛衫","雪纺衫","裤子","高跟鞋","袜子"]
        },
        yAxis: {},
        series: [{
          name: '销量',
          type: 'bar',
          data: [5, 20, 36, 10, 10, 20]
        }]
      });
    },
    methods: {
      ...mapActions([ 'changeBackTime'],),
      printTrigger(elementId) {
        var getMyFrame = document.getElementById(elementId);
        getMyFrame.focus();
        getMyFrame.contentWindow.print();
      },


      initData(){
        this.transparam();
        this.current = 0;
        this.active = 0;
        this.approvalStatus = '';

        let params = 'deviceId=' + this.$route.query.deviceId;
        avaivbleService.getDetailOrder(params).then(res => {
          if (res.data.useComName) {
            this.useComName = res.data.useComName;
          }
          this.deviceCategory = res.data.deviceCategory;
          console.log(this.deviceCategory)
          this.deviceClass = res.data.deviceClass;
          this.deviceKind = res.data.deviceKind;
          this.eqCode = res.data.eqCode;
          this.eqComCode = res.data.eqComCode;
          this.registCode = res.data.registCode;
          this.acceptorAgencyName = res.data.acceptorAgencyName;
          this.eqComCode = res.data.eqComCode;
          this.logs=res.data.logs;
          for(let i=0;i<this.logs.length;i++){
            console.log(this.logs[i].updateTime)
            this.changeBackTime( this.logs[i].updateTime);
            this.logs[i].updateTime=this.getBackTime;
          }


          this.applyType = res.data.applyType + "/" + res.data.deviceType;


        }).catch(error => {
          console.log(error)
        })



      },
      changeTime(time){
        return [time[0].getFullYear() + "-" + (parseInt(time[0].getMonth()) + 1) + "-" + time[0].getDate(),
          time[1].getFullYear() + "-" + (parseInt(time[1].getMonth()) + 1) + "-" + time[1].getDate()]

      },

      transparam(){
        if (this.$route.query.applyId) {
          this.applyId = this.$route.query.applyId;
        }
        if (this.$route.query.orderState) {
          this.orderState = this.$route.query.orderState;
          //console.log(this.orderState);
        }

      },
      appDetail(value){

          this.$router.push({
            path: 'appDetail',
            query: {
              applyId: value,
            }
          });

      },


    },

    computed: {
      ...mapState(['registOne']),
      ...mapGetters([
        "getBackTime"
      ]),
    },
    components: {
//      'v_regist_one': regist_one,
      'v-detailPdf': detailPdf,

    },

  }

</script>
<style lang="scss" scoped>
  @media print {
    .print {
      display: block;
    }

    .nprint {
      display: none;
    }
  }

  .deviceDetail {
    margin: 15px;
  }

  .comp_name {
    margin-bottom: 30px;
  }

  .setTable {
    margin-bottom: 30px;
    font-size: 17px;
  }

  .pdfdownload {
    margin-bottom: 30px;
  }

  .deviceDetailHead {
    margin-bottom: 15px;
    color: #1c2438;
  }

  .deviceContentHead {
    margin: 20px;
    color: #495060;
    font-size: 17px;

  }

  .applyDetailHead {
    margin-bottom: 15px;
    color: #1c2438;
  }

  .detailHead {
    margin: 20px 0;
    color: #1c2438;

  }

  .content {
    color: #495060;
    font-size: 17px;
  }

  .panel_content {
    font-size: small;
    color: #495060;
  }

  .acceptReason {
    margin-bottom: 20px;
  }

  .comp_name_content {
    font-size: 17px;
  }

  .base-box {
    margin: 0 auto;
    width: 100%;
    display: block;
    border: 2px solid #dddee1;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    border-bottom-right-radius: 3px;
    border-bottom-left-radius: 3px;
    //padding: 3px;

    padding-left: 10px;
    padding-right: 10px;
    padding-top: 10px;
    box-sizing: border-box;
    background-color: white;

  }

  .appDetail_topbar {
    position: fixed;
    width: 100%;
    margin: 0px;
    padding: 0px;
    height: 60px;
    z-index: 10;
    background-color: white;
  }
  .applyTypeHead{
    margin-bottom: 15px;
    color: #1c2438;
    font-size:23px;
  }
  .applyTypeContent{
    color: #1c2438;
    font-size:17px;

  }

</style>



